@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body{background:url(/react_pokedex/static/media/pokemonbg.6b36186f.png) no-repeat center center fixed;background-size:cover;font-family:"Press Start 2P",sans-serif;font-size:12px;line-height:1.5;text-transform:uppercase}input{width:80%;font-family:"Press Start 2P",sans-serif;background:rgba(250,250,250,0);border:none}input:focus{outline:none}.input-search{height:2rem;font-size:.7rem;text-transform:uppercase}.search-form{max-width:300px;padding:2px 2px 2px 10px;background:rgba(250,250,250,0.6);border-radius:20px;box-shadow:inset 1px 1px 3px dimgray}.caughtmon-btn{color:lightgray;background-color:#333A40;border:3px outset lightgray;border-radius:7px;padding:5px;text-align:center;margin-bottom:1.5rem}

.wrapper{margin:0 auto;max-width:750px;min-width:300px}.case{height:24vh;position:relative;border:11px inset;z-index:10;transition:top 1s}.case-top{background:red;border-color:#E40031;border-radius:30px 30px 0px 0px;border-bottom:0;display:flex;align-items:flex-end}.case-bottom{background:white;border-color:lightgray;border-radius:0px 0px 30px 30px;border-top:0}.circle-black{background-color:#000;width:160px;height:80px;border:10px solid white;margin:0 auto}.circle-blue{background-color:#E2F5FC;width:80px;height:40px;border:13px inset white;margin:0 auto}.top-outer{display:flex;align-items:flex-end;border-top-left-radius:160px;border-top-right-radius:160px;border-bottom:0}.top-inner{border-top-left-radius:80px;border-top-right-radius:80px;border-bottom:0}.bottom-outer{border-bottom-left-radius:160px;border-bottom-right-radius:160px;border-top:0}.bottom-inner{border-bottom-left-radius:80px;border-bottom-right-radius:80px;border-top:0}

.main-container{width:90%;height:48vh;margin:0 auto;background:rgba(226,245,252,0.6);box-shadow:inset 0px 5px 5px 3px #76D1E5}.outer-container{height:100%;margin:0 auto;padding:10px 15px 0 15px;box-shadow:inset -1px -8px 30px 6px white}

.navbar-container{padding-bottom:20px;display:flex}.navbar{width:100%;display:flex;align-items:center;justify-content:space-between}.navbar-mainbutton{height:1.3rem}

.pokemon{margin:0 auto;padding:5px 10px 2rem 10px;border-radius:8px;background-color:rgba(250,250,250,0.6);overflow:auto;height:35vh}.pokemon-arrows{display:flex;align-items:center;color:#555}.pokemon-header{font-size:.8rem;margin-bottom:.6rem}.pokemon-body{color:#555;font-size:.8rem}.pokemon-type{font-size:.7rem;color:white;padding:5px;border-radius:10px}.status{margin:0 auto;display:flex;justify-content:space-between;height:2rem}.status-bar{height:16px;border-radius:8px;background:cornflowerblue;box-shadow:inset 1px 1px 3px 6px midnightblue}.status-outline{height:16px;border-radius:8px;width:65%;box-shadow:inset 1px 1px 3px dimgray}.addpokemon-button{background-color:lightgray;border:3px inset darkgray;border-radius:7px;padding:5px;text-align:center;margin-bottom:1.5rem}

.trainer-stats{display:flex;flex-wrap:wrap;white-space:nowrap}.trainer-data{color:navy}.pokedex{display:grid;grid-template-columns:repeat(auto-fill, minmax(110px, 1fr))}.pokeball{margin:5px;padding-bottom:10px;border-radius:10px;background-color:rgba(255,255,255,0.5);font-size:.6rem;box-shadow:0 1px 2px 0 rgba(0,0,0,0.2)}

